import React, { useState, useRef, useEffect } from 'react';
import { 
  View, Text, StyleSheet, SafeAreaView, Image, ScrollView, 
  Animated, TouchableOpacity, Dimensions, Platform, ActivityIndicator, Alert
} from 'react-native';
import { useNavigation, useRoute } from '@react-navigation/native';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';
import PayPalWebView from '../components/PayPal/PayPalWebView';
import { getPayPalConfig } from '../components/PayPal/paypalConfig';
import Svg, { Path } from 'react-native-svg';

const { width: SCREEN_WIDTH } = Dimensions.get('window');

const PaymentScreen = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const [isLoading, setIsLoading] = useState(false);
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const [showPayPalModal, setShowPayPalModal] = useState(false);
  const [paypalUrl, setPaypalUrl] = useState('');
  const [isProcessing, setIsProcessing] = useState(false);
  
  // Get all parameters from the route
  const { 
    selectedHose,
    selectedHosePrice,
    selectedFitting,
    end1Shape,
    end1Size,
    end1Price,
    selectedEnd2Fitting,
    end2Shape,
    end2Size,
    end2Price,
    selectedAngle,
    quantity,
    cutLengths,
    cutLengthPrice,
    isDevMode: devModeActive,
    isOrderFittingMode,
    selectedProtection,
    hoseProtectionPrice,
    selectedPressure,
    pressureTestPrice,
    selectedMethod,
    selectedService,
    shippingCost,
    totalBeforeShipping,
    totalPriceWithShipping,
    protectionCost,
    shippingDetails
  } = route.params || {};

  // Simple price calculations
  const subtotal = isOrderFittingMode ? cutLengthPrice : totalBeforeShipping;
  const shippingAmount = devModeActive ? 0 : 12.85; // Set shipping to 0 if in dev mode
  const total = subtotal + shippingAmount;
  const gst = total * 0.10;
  const finalTotal = total + gst;

  // Initial fade-in animation
  useEffect(() => { 
    const animation = Animated.timing(fadeAnim, {
      toValue: 1,
      duration: 800,
      useNativeDriver: Platform.OS === 'native',
    });
    
    animation.start();
  
    return () => {
      animation.stop();
      fadeAnim.setValue(0);
    };
  }, []);

  const handlePayPalPress = async () => {
    if (isProcessing) return;
    
    try {
      setIsProcessing(true);
      console.log("Starting PayPal process...");
      
      const config = await getPayPalConfig();
      
      if (!config.merchantEmail) {
        console.error('PayPal merchant email not configured');
        setIsProcessing(false);
        return;
      }
      
      let description = 'Custom Hydraulic';
      if (selectedHose) {
        description += ` Hose - ${typeof selectedHose === 'object' ? selectedHose.name || 'Custom Hose' : selectedHose}`;
      }
      if (selectedFitting) {
        description += ` with ${typeof selectedFitting === 'object' ? selectedFitting.name || 'Custom Fitting' : selectedFitting}`;
      }
  
      const { baseUrl, merchantEmail } = config;
  
      // Build return URLs based on platform type
      let successUrl, cancelUrl;
      
      if (Platform.OS === 'web') {
        // Use simple and direct redirect URLs that point to our dedicated page
        const origin = window.location.origin;
        successUrl = `${origin}/paypal-redirect.html?success=true`; 
        cancelUrl = `${origin}/paypal-redirect.html?success=false`;
        
        console.log('PayPal success URL:', successUrl);
        console.log('PayPal cancel URL:', cancelUrl);
        
        // IMPORTANT: Store the order details in sessionStorage
        // Make sure we have comprehensive order details
        const orderDetails = {
          ...route.params,
          userDetails: shippingDetails,
          paymentId: Date.now().toString(),
          amount: finalTotal.toFixed(2),
          // Add extra verification data
          timestamp: new Date().toISOString(),
          checksum: 'order_' + Math.random().toString(36).substring(2)
        };
        
        // Log data size to verify it's substantial
        console.log('Storing order data:', JSON.stringify(orderDetails).length, 'bytes');
        
        try {
          // Clear any existing data first
          window.sessionStorage.removeItem('pendingOrderData');
          
          // Store new data
          window.sessionStorage.setItem('pendingOrderData', JSON.stringify(orderDetails));
          
          // Verify data was stored
          const stored = window.sessionStorage.getItem('pendingOrderData');
          if (stored) {
            console.log('Order data successfully stored in session');
          } else {
            console.error('Failed to verify stored data');
          }
        } catch (error) {
          console.error('Error storing order data in sessionStorage:', error);
        }
      }
  
      const queryParams = new URLSearchParams({
        cmd: '_xclick',
        business: merchantEmail,
        amount: finalTotal.toFixed(2),
        currency_code: 'AUD',
        item_name: description,
        return: successUrl,
        cancel_return: cancelUrl,
        rm: '1'  // Keep using GET
      }).toString();
  
      const paypalRedirectUrl = `${baseUrl}?${queryParams}`;
      
      if (!paypalRedirectUrl || !baseUrl || !merchantEmail) {
        console.error('Invalid PayPal configuration:', { paypalRedirectUrl, baseUrl, merchantEmail });
        setIsProcessing(false);
        return;
      }
  
      console.log('Redirecting to PayPal URL:', paypalRedirectUrl);
      
      // Choose navigation method based on platform
      if (Platform.OS === 'web') {
        // Direct browser redirect for web platform
        window.location.href = paypalRedirectUrl;
      } else {
        // For native platforms, continue using WebView
        setPaypalUrl(paypalRedirectUrl);
        setShowPayPalModal(true);
      }
    } catch (error) {
      console.error('Error in PayPal initialization:', error);
      setIsProcessing(false);
      Alert.alert(
        'PayPal Error',
        'Unable to initialize PayPal. Please try again later.'
      );
    }
  };

  const handleNavigationStateChange = (navState) => {
    if (navState.url.includes('payment_success') || navState.url.includes('payment_cancel')) {
      // Pass all route params along with payment details
      const orderDetails = {
        ...route.params,
        userDetails: shippingDetails,
        paymentId: Date.now().toString(),
        amount: finalTotal.toFixed(2)
      };

      navigation.reset({
        index: 0,
        routes: [
          {
            name: 'OrderConfirmation',
            params: {
              success: navState.url.includes('payment_success'),
            orderDetails: orderDetails, // Always pass orderDetails
            error: navState.url.includes('payment_cancel') ? 
              'Payment was cancelled' : 
              'Payment processing failed'
            }
          }
        ]
      });
      
      setIsProcessing(false);
      setShowPayPalModal(false);
      setPaypalUrl('');
    }
  };

  const renderShippingDetails = () => (
    <View style={styles.shippingDetailsContainer}>
      <View style={styles.sectionTitleContainer}>
        <Text style={styles.sectionTitleText}>Shipping Details</Text>
      </View>
      
      <View style={styles.detailsGrid}>
        {shippingDetails?.name && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Name:</Text>
            <Text style={styles.detailValue}>{shippingDetails.name}</Text>
          </View>
        )}
        
        {shippingDetails?.companyName && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Company:</Text>
            <Text style={styles.detailValue}>{shippingDetails.companyName}</Text>
          </View>
        )}
        
        {shippingDetails?.address && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Address:</Text>
            <Text style={styles.detailValue}>{shippingDetails.address}</Text>
          </View>
        )}
        
        {shippingDetails?.suburb && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Suburb:</Text>
            <Text style={styles.detailValue}>{shippingDetails.suburb}</Text>
          </View>
        )}
        
        {shippingDetails?.state && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>State:</Text>
            <Text style={styles.detailValue}>{shippingDetails.state}</Text>
          </View>
        )}
        
        {shippingDetails?.postcode && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Postcode:</Text>
            <Text style={styles.detailValue}>{shippingDetails.postcode}</Text>
          </View>
        )}

        {shippingDetails?.email && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Email:</Text>
            <Text style={styles.detailValue}>{shippingDetails.email}</Text>
          </View>
        )}
        
        {shippingDetails?.contactNumber && (
          <View style={styles.detailRow}>
            <Text style={styles.detailLabel}>Contact:</Text>
            <Text style={styles.detailValue}>{shippingDetails.contactNumber}</Text>
          </View>
        )}
      </View>
    </View>
  );

  const renderPriceBreakdown = () => (
    <View style={styles.priceBreakdownContainer}>
      <View style={styles.sectionTitleContainer}>
        <Text style={styles.sectionTitleText}>Price Breakdown</Text>
      </View>
      
      <View style={styles.priceDetailsGrid}>
        <View style={styles.priceRow}>
          <Text style={styles.priceLabel}>Subtotal:</Text>
          <Text style={styles.priceValue}>A${subtotal.toFixed(2)}</Text>
        </View>

        <View style={styles.priceRow}>
          <Text style={styles.priceLabel}>Shipping Cost:</Text>
          <Text style={styles.priceValue}>
            {shippingAmount > 0 ? `A$${shippingAmount.toFixed(2)}` : 'FREE'}
          </Text>
        </View>
        
        <View style={styles.priceRow}>
          <Text style={styles.priceLabel}>GST (10%):</Text>
          <Text style={styles.priceValue}>A${gst.toFixed(2)}</Text>
        </View>
      </View>
    </View>
  );

  return (
    <View style={styles.mainContainer}>
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Svg width="24" height="24" viewBox="0 0 24 24" fill="none">
          <Path 
            d="M19 12H5M12 19L5 12L12 5" 
            stroke="#4a4a4a" 
            strokeWidth="2.5" 
            strokeLinecap="round" 
            strokeLinejoin="round"
          />
        </Svg>
      </TouchableOpacity>
  
      <ScrollableGradientWrapper>
        <SafeAreaView style={styles.container}>
          <View style={styles.logoContainer}>
            <Image
              source={require('../assets/fluidpower_logo.gif')}
              style={styles.logo}
              resizeMode="contain"
            />
          </View>
          
          <Animated.View style={[styles.animatedContainer, { opacity: fadeAnim }]}>
            <ScrollView 
              contentContainerStyle={styles.scrollContent}
              showsVerticalScrollIndicator={true}
            >
              <View style={styles.titleContainer}>
                <Text style={styles.titleText}>Payment Details</Text>
              </View>
              
              <View style={styles.formContainer}>
                {renderShippingDetails()}
                {renderPriceBreakdown()}
  
                <View style={styles.totalContainer}>
                  <Text style={styles.totalText}>
                    Total Amount: A${finalTotal.toFixed(2)}
                  </Text>
                </View>
  
                <View style={styles.paypalContainer}>
                <TouchableOpacity 
                    style={styles.paypalButton}
                    onPress={handlePayPalPress}
                  >
                    <Text style={styles.paypalButtonText}>
                      {getPayPalConfig().isSandbox ? '(Sandbox) Proceed to Payment' : 'Proceed to Secure Payment'}
                    </Text>
                  </TouchableOpacity>
                  <Text style={styles.securePaymentText}>
                    {getPayPalConfig().isSandbox 
                      ? 'Test Mode - No real payment will be processed'
                      : 'You will be redirected to PayPal secure payment page, can pay using PayPal or Credit/Debit Card'}
                  </Text>
                </View>
              </View>
            </ScrollView>
          </Animated.View>
        </SafeAreaView>
      </ScrollableGradientWrapper>
  
      <PayPalWebView
        visible={showPayPalModal}
        paypalUrl={paypalUrl}
        onNavigationStateChange={handleNavigationStateChange}
        onRequestClose={() => {
          setShowPayPalModal(false);
          setPaypalUrl('');
          setIsProcessing(false);
        }}
      />
  
      {isProcessing && (
        <View style={styles.loadingOverlay}>
          <ActivityIndicator size="large" color="#4a4a4a" />
          <Text style={styles.loadingText}>Processing your payment...</Text>
        </View>
      )}
    </View>
  );
};

