import 'react-native-gesture-handler';
import React, { useRef, useEffect } from 'react';
import { View, Platform } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import './src/crypto-polyfill';
import { Font } from '@react-pdf/renderer';

import useEnvLoader from './src/useEnvLoader';
import { ActivityIndicator } from 'react-native';
import { Text } from 'react-native';

// Screen imports
import WelcomeScreen from './screens/WelcomeScreen';
import HoseSelectionScreen from './screens/HoseSelectionScreen';
import FittingSelectionScreen from './screens/FittingSelectionScreen';
import End1FittingScreen from './screens/End1FittingScreen';
import BSPFittingScreen1 from './screens/BSPFittingScreen1';
import JICFittingScreen1 from './screens/JICFittingScreen1';
import MetricFittingScreen1 from './screens/MetricFittingScreen1';
import OrfsScreen1 from './screens/OrfsScreen1';
import End2FittingScreen from './screens/End2FittingScreen';
import BSPFittingScreen2 from './screens/BSPFittingScreen2';
import JICFittingScreen2 from './screens/JICFittingScreen2';
import MetricFittingScreen2 from './screens/MetricFittingScreen2';
import OrfsScreen2 from './screens/OrfsScreen2';
import OrientationSelectionScreen from './screens/OrientationSelectionScreen';
import CutLengthsScreen from './screens/CutLengthsScreen';
import HoseProtectionScreen from './screens/HoseProtectionScreen';
import PressureTestingScreen from './screens/PressureTestingScreen';
import ShippingOptionsScreen from './screens/ShippingOptionsScreen';
import OrderConfirmationScreen1 from './screens/OrderConfirmationScreen1';
import ShippingDetailsScreen from './screens/ShippingDetailsScreen';
import PaymentScreen from './screens/PaymentScreen';
import OrderConfirmationScreen from './screens/OrderConfirmationScreen';
import ScrollTestScreen from './screens/ScrollTestScreen';
import TrialScreen2 from './screens/TrialScreen2';
import OrderDetailsScreen from './screens/OrderDetailsScreen';
import OrderDetailsScreen1 from './screens/OrderDetailsScreen1';
import PaymentSuccessPage from './components/PaymentSuccessPage';
import PaymentReturnScreen from './screens/PaymentReturnScreen';

// Register fonts for PDF generation
Font.register({
  family: 'Roboto',
  fonts: [
    { src: 'https://cdnjs.cloudflare.com/ajax/libs/ink/3.1.10/fonts/Roboto/roboto-light-webfont.ttf' },
    { src: 'https://cdnjs.cloudflare.com/ajax/libs/ink/3.1.10/fonts/Roboto/roboto-bold-webfont.ttf', fontWeight: 'bold' },
  ],
});

// Create the stack navigator
const Stack = createStackNavigator();

/**
 * Main application component
 */
const App = () => {
  const isEnvLoaded = useEnvLoader();
  const navigationRef = useRef(null);
  
  // Show loading spinner while env.js is being loaded
  if (!isEnvLoaded) {
    return (
      <View style={{ flex: 1, justifyContent: 'center', alignItems: 'center' }}>
        <ActivityIndicator size="large" />
        <Text>Loading Environment...</Text>
      </View>
    );
  }

  return (
    <SafeAreaProvider>
      <NavigationContainer 
        ref={navigationRef}
        onReady={() => {
          // Handle PayPal return navigation if needed
          if (Platform.OS === 'web') {
            const urlParams = new URLSearchParams(window.location.search);
            if (urlParams.get('screen') === 'OrderConfirmation') {
              // Get payment status and order details
              const success = urlParams.get('success') === 'true';
              let orderDetails = null;

              try {
                const orderDataString = window.sessionStorage.getItem('pendingOrderData');
                if (orderDataString) {
                  orderDetails = JSON.parse(orderDataString);
                  
                  // Clean up storage
                  window.sessionStorage.removeItem('pendingOrderData');
                }
              } catch (error) {
                console.error('Error retrieving order data:', error);
              }

              // Navigate to OrderConfirmation screen
              setTimeout(() => {
                navigationRef.current?.navigate('OrderConfirmation', {
                  success: success,
                  orderDetails: orderDetails,
                  error: !success ? 'Payment was cancelled' : null
                });
                
                // Clean up URL
                window.history.replaceState(null, '', window.location.pathname);
              }, 500);
            }
          }
        }}
      >
        <Stack.Navigator initialRouteName="Welcome">
          <Stack.Screen name="Welcome" component={WelcomeScreen} options={{ headerShown: false }} />
          <Stack.Screen name="HoseSelection" component={HoseSelectionScreen} options={{ headerShown: false }} />
          <Stack.Screen name="TrialScreen2" component={TrialScreen2} options={{ headerTransparent: true, headerBackground: () => ( <View style={{ flex: 1, backgroundColor: 'white' }} /> ), title: 'Trial Screen2' }} />
          <Stack.Screen name="FittingSelection" component={FittingSelectionScreen} options={{ headerTransparent: true, headerBackground: () => ( <View style={{ flex: 1, backgroundColor: 'white' }} /> ),  title: 'Select Fitting' }} />
          <Stack.Screen name="End1Fitting" component={End1FittingScreen} options={{ headerShown: false }} />
          <Stack.Screen name="BSPFittingScreen1" component={BSPFittingScreen1} options={{ headerShown: false }} />
          <Stack.Screen name="JICFittingScreen1" component={JICFittingScreen1} options={{ headerShown: false }} />
          <Stack.Screen name="MetricFittingScreen1" component={MetricFittingScreen1} options={{ headerShown: false }} />
          <Stack.Screen name="OrfsScreen1" component={OrfsScreen1} options={{ headerShown: false }} />
          <Stack.Screen name="End2Fitting" component={End2FittingScreen} options={{ headerShown: false }} />
          <Stack.Screen name="BSPFittingScreen2" component={BSPFittingScreen2} options={{ headerShown: false }} />
          <Stack.Screen name="JICFittingScreen2" component={JICFittingScreen2} options={{ headerShown: false }} />
          <Stack.Screen name="MetricFittingScreen2" component={MetricFittingScreen2} options={{ headerShown: false }} />
          <Stack.Screen name="OrfsScreen2" component={OrfsScreen2} options={{ headerShown: false }} />
          <Stack.Screen name="OrientationSelectionScreen" component={OrientationSelectionScreen} options={{ headerShown: false }} />
          <Stack.Screen name="CutLengths" component={CutLengthsScreen} options={{ headerShown: false }} />
          <Stack.Screen name="HoseProtection" component={HoseProtectionScreen} options={{ headerShown: false }} />
          <Stack.Screen name="PressureTesting" component={PressureTestingScreen} options={{ headerShown: false }} />
          <Stack.Screen name="ShippingOptions" component={ShippingOptionsScreen} options={{ headerShown: false }} />
          <Stack.Screen name="OrderConfirmation1" component={OrderConfirmationScreen1} options={{ headerShown: false }} />
          <Stack.Screen name="ShippingDetails" component={ShippingDetailsScreen} options={{ headerShown: false }} />
          <Stack.Screen name="Payment" component={PaymentScreen} options={{ headerShown: false }} />
          <Stack.Screen name="OrderConfirmation" component={OrderConfirmationScreen} options={{ headerLeft: null }} />
          <Stack.Screen name="OrderDetails" component={OrderDetailsScreen} options={{ title: 'Order Details', headerBackTitle: 'Back' }}/>
          <Stack.Screen name="OrderDetails1" component={OrderDetailsScreen1} options={{ headerShown: false }}/>
          <Stack.Screen name="payment_success" component={PaymentSuccessPage} options={{ headerShown: false }}/>
          <Stack.Screen name="PaymentSuccess" component={PaymentReturnScreen} options={{ headerShown: false }} />
          <Stack.Screen name="PaymentCancel" component={PaymentReturnScreen} options={{ headerShown: false }} />
        </Stack.Navigator>
      </NavigationContainer>
    </SafeAreaProvider>
  );
};

export default App;