import React, { useEffect, useState } from 'react';
import { View, Text, ActivityIndicator, StyleSheet } from 'react-native';
import { useNavigation, useRoute } from '@react-navigation/native';

const PaymentReturnScreen = () => {
  const navigation = useNavigation();
  const route = useRoute();
  const [errorMessage, setErrorMessage] = useState('');

  useEffect(() => {
    console.log('Current URL:', window.location.href);
    console.log('Current Path:', window.location.pathname);
    console.log('sessionStorage data:', window.sessionStorage.getItem('pendingOrderData'));
  
    // Rest of your existing code
  }, []);

  useEffect(() => {
    console.log('PaymentReturnScreen mounted. Route name:', route.name);
    console.log('Current URL:', window.location.href);
    
    // Rest of your code...
  }, [navigation, route.name]);
  
  useEffect(() => {
    // Determine if this is a success or cancel return
    const isSuccess = route.name === 'PaymentSuccess';
    
    // Get the order data from sessionStorage
    let orderDetails = {};
    if (typeof window !== 'undefined' && window.sessionStorage) {
      try {
        const orderDataString = window.sessionStorage.getItem('pendingOrderData');
        if (orderDataString) {
          orderDetails = JSON.parse(orderDataString);
          // Clear the data from sessionStorage
          window.sessionStorage.removeItem('pendingOrderData');
        } else {
          console.error('No order data found in sessionStorage');
          setErrorMessage('Order data not found. Please try again.');
          return;
        }
      } catch (error) {
        console.error('Error parsing order data from sessionStorage:', error);
        setErrorMessage('Error processing your order. Please try again.');
        return;
      }
    }
    
    // Short delay to allow the component to mount
    setTimeout(() => {
      // Navigate to the order confirmation screen
      navigation.reset({
        index: 0,
        routes: [
          {
            name: 'OrderConfirmation',
            params: {
              success: isSuccess,
              orderDetails: orderDetails,
              error: !isSuccess ? 'Payment was cancelled' : null
            }
          }
        ]
      });
    }, 500);
  }, [navigation, route.name]);
  
  return (
    <View style={styles.container}>
      <ActivityIndicator size="large" color="#4a4a4a" />
      <Text style={styles.loadingText}>
        {errorMessage || (route.name === 'PaymentSuccess' 
          ? 'Payment successful! Processing your order...' 
          : 'Returning to checkout...')}
      </Text>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#fff',
    padding: 20
  },
  loadingText: {
    marginTop: 20,
    fontSize: 18,
    color: '#4a4a4a',
    textAlign: 'center'
  }
});

export default PaymentReturnScreen;