import { useEffect, useState } from 'react';

const useEnvLoader = () => {
  const [isEnvLoaded, setIsEnvLoaded] = useState(false);

  useEffect(() => {
    const loadEnvScript = () => {
      return new Promise((resolve, reject) => {
        const script = document.createElement('script');
        script.src = 'http://localhost:3001/env.js';  // Reference to your env.js
        script.async = true;
        script.onload = () => resolve(true);
        script.onerror = (err) => reject(err);
        document.head.appendChild(script);
      });
    };

    loadEnvScript()
      .then(() => {
        setIsEnvLoaded(true);
      })
      .catch((error) => {
        console.error('Error loading env.js:', error);
        setIsEnvLoaded(true);  // We can still proceed even if it fails
      });
  }, []);

  return isEnvLoaded;
};

export default useEnvLoader;
